
html,body {
    margin: 0 auto;
    padding: 0;
    height: 1120px;
}
p,span{
    margin: 0;
    padding: 0;
    font-family: Arial, Helvetica, sans-serif;
}
#app {
    width: 800px;
    height: 1130px;
    overflow: hidden;
    margin: 0 auto;
}
.left-pannel {
    position: relative;
    width: 320px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.right-pannel {
    width: 480px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    display: inline-block;
}
.pannel {
    float: left;
    padding: 20px;
    height: 1140px;
    padding-top: 40px;
}

.left-base {
    background: #c3985d;
}
.flex-center {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.flex-center-column {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
}
.name {
    height: 80px;
    width: 100%;
}
.name p {
        font-size: 70px;
        color: white;
}
.age {
    height: 40px;
    font-size: 20px;
}
.age span {
        margin-left: 10px;
        color: #936d3d;
}
.age i {
        font-size: 24px;
        margin-left: 5px;
        color: #936d3d;
}
.education {
    padding: 8px;
    color: #835f2e;
    border-color: rgba(131, 95, 46, .33333);
    border-style: solid;
    border-width: 2px 0 1px 0;
    margin: 0 30px 0 30px;
}
.education p {
        white-space: nowrap;
        font-size: 24px;
}
.education p +p {
            font-size: 18px;
}
.education p +p +p {
                font-size: 14px;
}
.position {
    margin: 16px;
    font-size: 24px;
    color: #f6e2b9;
}
.intro {
    width: 100%;
}
.intro img {
        width: 80%;
}

.skill-progress {
    width: 80%;
    margin: 0 auto;
    -webkit-transform: translateX(-6%);
            transform: translateX(-6%);
}
.skill-progress .skill-bar {
        width: 100%;
        height: 260px;
}
.skill-progress .learning {
        margin: 0px auto;
        text-align: center;
        -webkit-transform: translateX(10%);
                transform: translateX(10%);
}
.skill-progress .learning img {
            width: 100px;
}

.echarts {
  width: 600px;
  height: 400px;
}

.foot {
    position: absolute;
    bottom: 20px;
    left: 40px;
}
.foot .foot-list {
        -webkit-box-align: center;
            -ms-flex-align: center;
                align-items: center;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        color: white;
        padding-right: 20px;
        margin-bottom: 16px;
}
.foot .foot-list span {
            word-break: break-word;
            display: inline-block;
            margin-left: 10px;
            font-size: 10px;
}


.circle {
    display: inline-block;
    width: 30px;
    height: 30px;
    background-color: white;
    border-radius: 50%;
}
.circle i {
        color: #c3985d;
}
.circle .container {
        width: 30px;
        height: 30px;
}
.flex-center {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}

.ability-wordcloud {
    width: 90%;
    height: 260px;
    margin: 0 auto;
}

.right-detail {
    background: #f1eee8;
    padding-top: 60px;
    padding-left: 50px;
    padding-right: 40px;
}

.right-section {
    position: relative;
}
.right-section .title-icon {
        position: absolute;
        left: -32px;
        top: 6px;
        color: #c3985d;
        font-size: 25px;
}
.right-section .title {
        font-size: 30px;
        color: #6b6a7b;
}
.right-section .project-block {
        margin-bottom: 24px;
}
.right-section .project-block .project-tag {
            font-size: 12px;
}
.right-section .project-block .project-name {
            font-size: 20px;
            color: #c3985d;
            margin-bottom: 6px;
}
.right-section .project-block .project-date-range {
            font-size: 14px;
            color: #c3985d;
            margin-bottom: 4px;
}
.right-section .project-block .project-context {
            font-size: 14px;
}
.right-section .project-block .context-indent {
            text-indent: 28px;
}


.tag {
    background: #c3985d;
    display: inline-block;
    border-radius: 5px;
    padding: 2px 4px;
    color: #f6e2b9;
    margin-bottom: 10px;
    margin-right: 5px;
}
